import React from "react";
import "./footer.css";
import companyLogo from "../../img/hayoke-pt-footer.png";

const CompanyFooter = () => {
  return (
    <>
      <div className="footer-wrapper">
        <img
          src={companyLogo}
          alt=""
          style={{
            width: "250px",
            height: "250px",
          }}
        />
        <div className="footer-information">
          <div className="email-wrapper">
            <div>Email</div>
            <div className="email">info@hayokeglobaltrade.com</div>
          </div>
          <div className="telephone-wrapper">
            <div>Telephone</div>
            <div className="telephone">+62 817-0060-803</div>
          </div>
          <div className="address-wrapper">
            <div>Address</div>
            <div className="address">
              Pulo Gebang Permai Jl. Pulo Halmahera Blok D5 No. 24, Cakung -
              Jakarta Timur 13950
            </div>
          </div>
        </div>
        {/* <div className="footer-socmed">
          <div className="linkedin-wrapper">
            <img src="" alt="" />
            <div>Hayoke Global Trade</div>
          </div>
        </div> */}
      </div>
    </>
  );
};

export default CompanyFooter;
